import React from 'react';
import { Routes, Route, BrowserRouter } from 'react-router-dom';
import Layout from './hocs/Layout';

import Login from './containers/Login'
import Signup from './containers/Signup'
import Meallog from './containers/Meallog'
import Home from './containers/Welcome'
import Workout from './containers/Workout'
import Logout from './containers/Logout'
import Goal from './containers/Goal'

const App = () => {
    return (
    <div>
        <BrowserRouter>
            <Routes>
                <Route path='/LogMe' element = {<Layout />}/>
                <Route path='/logme/log-in' element={<Login />}/>
                <Route path='/logme/signup' element={<Signup />}/>
                <Route path='/logme/meals' element={<Meallog />} />
                <Route path='/logme/welcome' element={<Home />} />
                <Route path='/logme/workouts' element={<Workout /> } />
                <Route path='/logme/goals' element={<Goal />} />
                <Route path='/logme/logout' element={<Logout />} />
            </Routes>
        </BrowserRouter>    
    </div>
);
}


export default App;