import React, { useState } from 'react';
import { register } from '../actions/auth';
import { connect } from 'react-redux';
import { Link } from'react-router-dom'

// Redirect is now Navigate in the module
import { Navigate } from 'react-router-dom';
import CSRFToken from '../components/CSRFToken';

const Signup = ({ register }) => {
    const [formData, setFormData] = useState({
        username: '',
        password: '',
        re_password: ''
    });

    const [accountCreated, setAccountCreated] = useState(false);

    const { username, password, re_password } = formData

    // Allow the state to update with the User input
    const onChange = e => setFormData({...formData, [e.target.name]: e.target.value })

    const onSubmit = e => {
        e.preventDefault();

        if (password === re_password) {
            register(username, password, re_password);
            setAccountCreated(true);
        }
    };
    
    if (accountCreated)
        return <Navigate to='/logme/welcome' />
    // if (isAuthenticated)
    //     return <Redirect to= '/welcome' />
    // else if (accountCreated)
    //     return <Redirect to='/login' />;

    return(
    <div className="container-fluid" id="wrapper">
        <div className="login_registration_card" id="registration_card2">
        <div className="box">
        <p id="title">Registration </p>
        <form  onSubmit={e => onSubmit(e)}>
            <CSRFToken />
            <p> 
                <label>Email: </label>
                <input 
                    type="email" 
                    name="email" 
                    id="email_logo" 
                    placeholder="Email Address" 
                    className="field" 
                    required 
                    value={username}
                    onChange={e => onChange(e)}
                />
            </p>
            <p> 
                <label>Password: </label>
                <input 
                    type="text" 
                    name="pw" 
                    minlength="8" 
                    id="pw_logo" 
                    placeholder="Password" 
                    className="field" 
                    required 
                    onChange={e => onChange(e)} 
                    value={password} 
                />
            </p>
            <p> 
                <label>Confirm Password: </label>
                <input 
                    type="text" 
                    name="cpw" 
                    maxlength="64" 
                    placeholder="Confirm Password" 
                    className="field" 
                    required 
                    id="id_cpw" 
                    onChange={e => onChange(e)} 
                    value={re_password}
                />
            </p>
            <button type="submit" className="button">Register</button>
        </form>
        <p>
            <Link to="/logme/login">Back to Login</Link>
        </p>
        </div>
        </div>
    </div>
    );
};

export default connect(null, { register })(Signup);

