import {
  REGISTER_SUCCESS,
  REGISTER_FAIL
} from '../actions/types'

const initialState = {
  isAuthenticated: null,
  username: '',
  first_name: '',
  last_name: '',
  email: '',
  profile_name: ''
};

export default function(state = initialState, action){
  // destruct your action
  const { type, payload } = action;

  switch (type) {
    case REGISTER_SUCCESS:
      // in this example, if you are registered if you are signed in then you should not be able to regester for a
      return {
        ...state,
        isAuthenticated: false
      }
    case REGISTER_FAIL:
        return state
    default:
        return state
  };
};